import { gql } from '@apollo/client';

export const GET_ROUNDS_HASH_LOTTERY = gql`
  query GetRounds($perPage: Int!, $skip: Int!) {
    rounds(first: $perPage, skip: $skip, orderBy: roundID, orderDirection: desc) {
      id
      block
      hash
      hashMap
      drawnBy
      rewards
      numberOfWinners
      totalBets
      roundID
      tx
    }

    summaries {
      totalRounds
    }
  }
`;

export const GET_PREVIOUS_BETS = gql`
  query GetBets($perPage: Int!, $skip: Int!) {
    bets(first: $perPage, skip: $skip, orderBy: block, orderDirection: desc, where: { type: "Block Hash Game" }) {
      id
      betID
      user {
        id
      }
      amount
      reward
      bet
      hash
      win
      block
      tx
    }

    summaries {
      totalBets
    }
  }
`;

