export const GET_USER_BETS = gql`
  query GetUserBets($perPage: Int!, $skip: Int!, $account: String!) {
    bets(first: $perPage, skip: $skip, orderBy: block, orderDirection: desc, where: { user: $account }) {
      id
      betID
      amount
      reward
      bet
      hash
      win
      block
      tx
      type
      shell
      envelope
      mode
      slot
      token
    }

    users(where: { id: $account }) {
      totalBets
    }
  }
`;

export const GET_USER_TICKETS_HASH_LOTTERY = gql`
  query GetUserBets($perPage: Int!, $skip: Int!, $account: String!) {
    tickets(first: $perPage, skip: $skip, orderBy: block, orderDirection: desc, where: { user: $account }) {
      id
      block
      betID
      user {
        id
        totalTickets
      }

      round {
        id
        hash
        hashMap
        rewards
        numberOfWinners
        totalBets
        roundSettled
      }

      hash
      hashMap
      amount
      tx
    }

    users(where: { id: $account }) {
      totalTickets
    }
  }
`;

export const GET_PAST_JACKPOTS = gql`
  query GetBets($perPage: Int!, $skip: Int!, $status: Boolean!) {
    bets(
      first: $perPage
      skip: $skip
      orderBy: block
      orderDirection: desc
      where: { win: $status, type: "Block Hash Game" }
    ) {
      id
      betID
      user {
        id
      }
      amount
      reward
      bet
      hash
      win
      block
      tx
    }

    summaries {
      totalJackpots
    }
  }
`;

