import { useState, useEffect } from 'react';

// Material
import { styled } from '@material-ui/core/styles';
import {
  Box,
  Tab,
  Tabs,
  Container,
  Chip,
  Stack,
  Typography,
  TextField,
  InputAdornment,
  Button,
  Dialog,
  DialogTitle,
  DialogActions,
  DialogContent,
  DialogContentText
} from '@material-ui/core';

import { TabContext, TabList, TabPanel } from '@material-ui/lab';
import { useEthers, useTokenAllowance, useTokenBalance } from '@usedapp/core';
import { utils, BigNumber } from 'ethers';
import { LOTO_STAKING_INTERFACE, LOTO_STAKING_ADDRESS, LOTO_STAKING_CONTRACT, LOTO_TOKEN_ADDRESS } from '../config';

import ApproveButton from './ApproveButton';
import StakeButton from './StakeButton';
import UnstakeButton from './UnstakeButton';

// Components
import Label from './Label';

import { fCRCurrency } from '../utils/formatNumber';

const SIMPLE_TAB = [
  { value: '1', label: 'Stake', disabled: false },
  { value: '2', label: 'Unstake', disabled: false }
];

const Staking = (props) => {
  const { account } = useEthers();

  const [dropAllowance, setDropAllowance] = useState(undefined);

  const [dropBalance, setDropBalance] = useState('0');

  const [stakedDropBalance, setStakedDropBalance] = useState('0');

  const allowance = useTokenAllowance(LOTO_TOKEN_ADDRESS, account, LOTO_STAKING_ADDRESS);

  const balance = useTokenBalance(LOTO_TOKEN_ADDRESS, account);

  const stakedBalance = useTokenBalance(LOTO_STAKING_ADDRESS, account);

  const [value, setValue] = useState('1');

  const [amount, setAmount] = useState('');

  const [stakedAmount, setStakedAmount] = useState('');

  useEffect(() => {
    if (allowance) {
      setDropAllowance(allowance);
    }

    if (balance) {
      setDropBalance(utils.formatEther(balance.toString()));
    }

    if (stakedBalance) {
      setStakedDropBalance(utils.formatEther(stakedBalance.toString()));
    }
  }, [allowance, balance, stakedBalance]);

  // Alert Dialog
  const [open, setOpen] = useState(false);
  const handleClose = () => {
    setOpen(false);
  };

  // Success or Failed Dialog
  const [openStakeStatus, setOpenStakeStatus] = useState(false);
  const [stakeStatusMsg, setStatusMsg] = useState({ message: '', icon: '' });
  const handleCloseStakeStatus = () => {
    setOpenStakeStatus(false);
  };

  const failedStakeStatusMsg = (msg) => {
    setStatusMsg({ message: msg, icon: 'failedWarning.svg', title: 'Failed' });
    setOpenStakeStatus(true);
  };

  const successStakeStatusMsg = (msg) => {
    setStatusMsg({ message: msg, icon: 'success.svg', title: 'Success' });
    setOpenStakeStatus(true);
  };

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const handleChangeValue = (value) => {
    setAmount(value);
  };

  const handleSetMax = () => {
    setAmount(dropBalance);
  };

  const handleChangeStakedValue = (value) => {
    setStakedAmount(value);
  };

  const handleSetStakedMax = () => {
    setStakedAmount(stakedDropBalance);
  };

  const timelockDate = new Date(props.timeLock * 1000);

  const timelockMonth = ('0' + (timelockDate.getMonth() + 1)).substr(-2);
  const timelockDay = ('0' + timelockDate.getDate()).substr(-2);

  const timelockDateHour = ('0' + timelockDate.getHours()).substr(-2);
  const timelockDateMinutes = ('0' + timelockDate.getMinutes()).substr(-2);
  const timelockDateseconds = ('0' + timelockDate.getSeconds()).substr(-2);

  return (
    <Stack spacing={3}>
      <TabContext value={value}>
        <TabList onChange={handleChange} centered>
          {SIMPLE_TAB.map((tab, index) => (
            <Tab
              key={tab.value}
              label={tab.label}
              value={String(index + 1)}
              style={{ fontSize: '1.5rem', padding: '0.5rem' }}
            />
          ))}
        </TabList>
        <Box
          sx={{
            p: '2rem',
            mt: 2,
            width: '100%',
            borderRadius: 1,
            bgcolor: 'grey.5008'
          }}
        >
          <TabPanel key="1" value={String(1)}>
            <Stack>
              <Typography variant="h4">Stake $JUICE</Typography>

              <Label
                variant="filled"
                color="primary"
                style={{ marginBottom: '1rem', width: 'min-content', marginTop: '0.2rem' }}
              >
                1 JUICE = 1 Share
              </Label>

              <Box sx={{ mt: 2, mb: 2, display: 'flex' }}>
                <TextField
                  style={{ width: '100%', margin: 'auto' }}
                  label="Stake Amount"
                  InputProps={{
                    endAdornment: (
                      <InputAdornment position="end">
                        <Button variant="text" onClick={(e) => handleSetMax()}>
                          MAX
                        </Button>
                      </InputAdornment>
                    )
                  }}
                  value={amount}
                  onChange={(e) => handleChangeValue(e.target.value)}
                  type="number"
                />
              </Box>

              <Chip label={`Max: ${fCRCurrency(dropBalance)} $JUICE`} style={{ width: 'min-content' }} />
              {dropAllowance &&
              amount !== '' &&
              !utils.parseEther(amount).eq(BigNumber.from('0')) &&
              dropAllowance.gte(utils.parseEther(amount)) ? (
                <>
                  <StakeButton
                    setOpen={setOpen}
                    value={amount}
                    account={account}
                    balance={dropBalance}
                    setAmount={setAmount}
                    openSuccessPopup={successStakeStatusMsg}
                    openFailedPopup={failedStakeStatusMsg}
                  />

                  <ApproveButton
                    setOpen={setOpen}
                    value={amount}
                    account={account}
                    balance={dropBalance}
                    openSuccessPopup={successStakeStatusMsg}
                    openFailedPopup={failedStakeStatusMsg}
                    style={{ display: 'none' }}
                  />
                </>
              ) : (
                <>
                  <ApproveButton
                    setOpen={setOpen}
                    value={amount}
                    account={account}
                    balance={dropBalance}
                    openSuccessPopup={successStakeStatusMsg}
                    openFailedPopup={failedStakeStatusMsg}
                  />
                  <StakeButton
                    style={{ display: 'none' }}
                    setOpen={setOpen}
                    value={amount}
                    account={account}
                    balance={dropBalance}
                    setAmount={setAmount}
                    openSuccessPopup={successStakeStatusMsg}
                    openFailedPopup={failedStakeStatusMsg}
                  />
                </>
              )}
            </Stack>
          </TabPanel>

          <TabPanel key="2" value={String(2)}>
            <Stack>
              {!props.lockUnstake ? (
                <>
                  <Typography variant="h4">Unstake $JUICE</Typography>
                  <Label
                    variant="filled"
                    color="primary"
                    style={{ marginBottom: '1rem', width: 'min-content', marginTop: '0.2rem' }}
                  >
                    1 $JUICE = 1 Share
                  </Label>

                  <Box sx={{ mt: 2, mb: 2, display: 'flex' }}>
                    <TextField
                      style={{ width: '100%', margin: 'auto' }}
                      label="Unstake Amount"
                      InputProps={{
                        endAdornment: (
                          <InputAdornment position="end">
                            <Button variant="text" onClick={(e) => handleSetStakedMax()}>
                              MAX
                            </Button>
                          </InputAdornment>
                        )
                      }}
                      type="number"
                      value={stakedAmount}
                      onChange={(e) => handleChangeStakedValue(e.target.value)}
                    />
                  </Box>

                  <Chip label={`Staked: ${fCRCurrency(stakedDropBalance)} $JUICE`} style={{ width: 'min-content' }} />

                  <UnstakeButton
                    setOpen={setOpen}
                    value={stakedAmount}
                    account={account}
                    balance={stakedDropBalance}
                    setAmount={setStakedAmount}
                    openSuccessPopup={successStakeStatusMsg}
                    openFailedPopup={failedStakeStatusMsg}
                  />
                </>
              ) : (
                <Typography variant="h6">
                  'Unstake window will open for 1 hour from {timelockDay}.{timelockMonth} at {timelockDateHour}:
                  {timelockDateMinutes}:{timelockDateseconds}
                </Typography>
              )}
            </Stack>
          </TabPanel>
        </Box>
      </TabContext>

      <Dialog open={open} onClose={handleClose}>
        <DialogTitle>Wallet Not Connected</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">Connect a wallet first to stake/unstake!</DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} autoFocus>
            Ok
          </Button>
        </DialogActions>
      </Dialog>

      <Dialog open={openStakeStatus} onClose={handleCloseStakeStatus}>
        <DialogTitle>{stakeStatusMsg.title}</DialogTitle>
        <DialogContent>
          <img
            alt=""
            src={`/static/svg/${stakeStatusMsg.icon}`}
            style={{ width: '8rem', margin: 'auto', marginTop: '2rem' }}
          />
          <DialogContentText
            id="alert-dialog-description"
            style={{ textAlign: 'center', marginTop: '2rem', fontSize: '1.2rem' }}
          >
            {stakeStatusMsg.message}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseStakeStatus} autoFocus>
            Ok
          </Button>
        </DialogActions>
      </Dialog>
    </Stack>
  );
};

export default Staking;
