import { useState, useEffect } from 'react';
import { utils, BigNumber } from 'ethers';

import { Box, Button } from '@material-ui/core';

import { useContractFunction, useEthers } from '@usedapp/core';

import { LOTO_STAKING_CONTRACT } from '../config';

const ClaimButton = (props) => {
  const [disabled, setDisabled] = useState(true);

  const [usingDisabled, setUsingDisabled] = useState(true);

  const [buttonText, setButtonText] = useState('CLAIM');

  const { state, send } = useContractFunction(LOTO_STAKING_CONTRACT, 'claim', { transactionName: 'Claim Drop' });

  useEffect(() => {
    if (Number(props.drop) > 0.00001) {
      setDisabled(false);
      setUsingDisabled(false);
    } else {
      setDisabled(true);
      setUsingDisabled(true);
    }
  }, [props.drop]);

  useEffect(() => {
    if (state.status === 'Exception' || state.status === 'Fail') {
      if (!usingDisabled) {
        setDisabled(false);
      }
      setButtonText('CLAIM');

      props.openFailedPopup('Failed to claim BNB drops');
    }

    if (state.status === 'None') {
      if (!usingDisabled) {
        setDisabled(false);
      }
      setButtonText('CLAIM');
    }

    if (state.status === 'Success') {
      setButtonText('CLAIM');
      props.openSuccessPopup('Successfully claimed BNB drops');
    }

    if (state.status === 'Mining') {
      setDisabled(true);
      setUsingDisabled(false);
      setButtonText('PENDING...');
    }
  }, [state]);

  const handleClaim = () => {
    send();
  };

  return (
    <Button
      variant="contained"
      color="primary"
      style={{ width: '50%', marginTop: '0.5rem', maxWidth: '300px' }}
      disabled={disabled}
      onClick={handleClaim}
    >
      {buttonText}
    </Button>
  );
};

export default ClaimButton;
