import { useState, useEffect } from 'react';
import { utils, BigNumber } from 'ethers';

import { Box, Button } from '@material-ui/core';

import { useContractFunction } from '@usedapp/core';

import { LOTO_STAKING_CONTRACT } from '../config';

const StakeButton = (props) => {
  const [disabled, setDisabled] = useState(true);

  const [usingDisabled, setUsingDisabled] = useState(true);

  const [buttonText, setButtonText] = useState('STAKE');

  const value = props.value !== '' ? utils.parseEther(props.value) : BigNumber.from('0');

  const balance = utils.parseEther(props.balance);

  useEffect(() => {
    if (value.gt(BigNumber.from('0'))) {
      setDisabled(false);
      setUsingDisabled(false);
    } else {
      setDisabled(true);
      setUsingDisabled(true);
    }

    if (props.account) {
      if (value.gt(balance)) {
        setDisabled(true);
        setUsingDisabled(true);
        setButtonText('INSUFFICIENT BALANCE');
      } else if (balance.eq(BigNumber.from('0')) || value.eq(BigNumber.from('0'))) {
        setDisabled(true);
        setUsingDisabled(true);
      } else {
        setDisabled(false);
        setUsingDisabled(false);
        setButtonText('STAKE');
      }
    } else {
      setDisabled(true);
      setUsingDisabled(true);
    }
  }, [props.value, props.account, props.balance]);

  const { state, send } = useContractFunction(LOTO_STAKING_CONTRACT, 'enter', { transactionName: 'Stake Drop' });

  useEffect(() => {
    if (state.status === 'Exception' || state.status === 'Fail') {
      if (!usingDisabled) {
        setDisabled(false);
      }
      setButtonText('STAKE');

      props.openFailedPopup('Failed to stake $JUICE');
    }

    if (state.status === 'None') {
      if (!usingDisabled) {
        setDisabled(false);
      }
      setButtonText('STAKE');
    }

    if (state.status === 'Success') {
      setButtonText('STAKE');
      props.setAmount('');

      props.openSuccessPopup('Successfully staked $JUICE');
    }

    if (state.status === 'Mining') {
      setDisabled(true);
      setUsingDisabled(false);
      setButtonText('PENDING...');
    }
  }, [state]);

  const handleStake = () => {
    if (props.account) {
      props.setOpen(false);
      send(value);
    } else {
      props.setOpen(true);
    }
  };

  return (
    <Box mt="auto" style={props.style}>
      <Button
        size="large"
        variant="contained"
        color="primary"
        style={{ width: '100%', marginTop: '4rem' }}
        disabled={disabled}
        onClick={handleStake}
      >
        {buttonText}
      </Button>
    </Box>
  );
};

export default StakeButton;
